body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.nav__input{font-family:"Patrick Hand SC",cursive;height:4vh;width:80%;margin-left:1vw;font-size:1.2rem}
.sidenav{height:100%;width:20vw;position:fixed;z-index:1;top:0;left:0;background-color:#111;overflow-x:hidden;padding-top:20px}.sidenav a,.dropdown-btn{padding:6px 8px 6px 16px;text-decoration:none;font-size:20px;color:#818181;display:block;border:none;background:none;width:100%;text-align:left;cursor:pointer;outline:none}.sidenav a:hover,.dropdown-btn:hover{color:#f1f1f1}
.rankings{width:75vw;display:flex;margin-top:100px;display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;grid-gap:10px 20px;gap:10px 20px;margin-left:20vw}.rankings__header{display:flex;flex-direction:column;justify-content:space-around;align-items:center;height:60vh;text-align:center;font-family:"Patrick Hand SC",cursive}.surfer{background-color:#34568b;border-radius:20px;height:50vh;margin-bottom:50px;flex-direction:row;align-items:center;justify-content:center;text-align:center;font-family:"Patrick Hand SC",cursive}.surfer__name{font-weight:900}img{height:20vh;width:auto}
.profiles{width:75vw;display:flex;margin-top:100px;display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;grid-gap:10px 20px;gap:10px 20px;margin-left:20vw}.profiles__header{display:flex;flex-direction:column;justify-content:space-around;align-items:center;height:60vh;text-align:center;font-family:"Patrick Hand SC",cursive}.surfer{font-family:"Patrick Hand SC",cursive;background-color:rgba(52,86,139,.7);margin-bottom:50px;height:70vh;border-radius:20px;display:flex;justify-content:flex-start;align-content:center;flex-direction:column}.surfer__name{font-size:1.3rem;font-weight:900;text-align:center}.surfer__age{font-size:1rem}img{margin-top:10px;justify-self:center;align-self:center;height:20vh;width:auto}
.addSurfer{margin-left:20vw;height:180vh;font-family:"Patrick Hand SC",cursive;-webkit-transform:translateZ(0);transform:translateZ(0);text-align:center;background-color:rgba(52,86,139,.7);display:flex;flex-direction:column;justify-content:flex-start;align-items:center;width:80vw}.addSurfer__header{font-family:"Patrick Hand SC",cursive;font-weight:700;text-transform:uppercase;letter-spacing:4px;text-align:center}.addSurfer__sub-header{font-weight:400;font-size:24px;color:rgba(255,255,255,.4)}.form-footer{margin-top:2em}.ui-input{position:relative;padding:0;border:0}.ui-input input{font-family:"Patrick Hand SC",cursive;border:0;background:none;padding:16px 0 16px 0;font-size:24px;outline:0;width:100%}.ui-input input+label{position:relative;display:block;padding:8px 0 8px 0;text-transform:uppercase;font-size:14px;letter-spacing:.0875em;font-weight:500;text-align:left}.ui-input input+label::before,.ui-input input+label::after{position:absolute;top:0;left:0;content:"";width:100%;height:1px}.ui-input input+label::before{background-color:rgba(255,255,255,.6)}.ui-input input+label::after{-webkit-transform:scaleX(0);transform:scaleX(0);-webkit-transform-origin:left;transform-origin:left;background-color:#6eb1ff;height:2px}.ui-input input+label span{position:relative;color:rgba(255,255,255,.6)}.ui-input input:focus+label::after,.ui-input input.error+label::after,.ui-input input:invalid+label::after,.ui-input input.filled+label::after{-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transform-origin:left;transform-origin:left}.ui-input input:focus+label span::after,.ui-input input.error+label span::after,.ui-input input:invalid+label span::after,.ui-input input.filled+label span::after{background-image:linear-gradient(to right, white 50%, rgba(255, 255, 255, 0) 0%);background-position:0% 50%}.ui-input input.filled{color:#fff}.ui-input input.filled+label::after{background-color:#fff}.ui-input input.filled+label span::after{background-image:linear-gradient(to right, #ffffff 50%, rgba(255, 255, 255, 0) 0%);background-position:0% 50%}.ui-input input:focus{color:#6eb1ff}.ui-input input:focus+label::after{background-color:#6eb1ff}.ui-input input:focus+label span::after{background-image:linear-gradient(to right, #6EB1FF 50%, rgba(255, 255, 255, 0) 0%);background-position:0% 50%}.ui-input input.error,.ui-input input:invalid{color:#e66161}.ui-input input.error+label::after,.ui-input input:invalid+label::after{background-color:#e66161}.ui-input input.error+label span::after,.ui-input input:invalid+label span::after{background-image:linear-gradient(to right, #E66161 50%, rgba(255, 255, 255, 0) 0%);background-position:0% 50%}.btn{border:0;background-color:#50617a;padding:18px 30px;font-size:14px;line-height:1.5;text-transform:uppercase;letter-spacing:.0875em;font-weight:500;color:#fff;font-family:"Patrick Hand SC",cursive;border-radius:100px;outline:0}.btn:focus,.btn:active,.btn:hover{background-color:#6eb1ff;color:#fff}.__first,.__second,.__third,.__fourth{-webkit-animation-name:fadeIn;animation-name:fadeIn;-webkit-animation-duration:180ms;animation-duration:180ms;-webkit-animation-fill-mode:both;animation-fill-mode:both;-webkit-animation-iteration-count:1;animation-iteration-count:1}.__first{-webkit-animation-delay:0;animation-delay:0}.__second{-webkit-animation-delay:80ms;animation-delay:80ms}.__third{-webkit-animation-delay:180ms;animation-delay:180ms}.__fourth{-webkit-animation-delay:360ms;animation-delay:360ms}@-webkit-keyframes fadeIn{from{opacity:0;-webkit-transform:translate3d(0, -25%, 0);transform:translate3d(0, -25%, 0)}to{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@keyframes fadeIn{from{opacity:0;-webkit-transform:translate3d(0, -25%, 0);transform:translate3d(0, -25%, 0)}to{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}
.main{margin-left:20vw;display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh}.main__quote{font-family:"Patrick Hand SC",cursive;font-size:5rem;text-align:center;width:80vw;height:auto}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

